// Version:     7.xx
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Country from './components/Country';
import NewCountry from './components/NewCountry';
import Container from '@material-ui/core/Container';
import { Grid } from '@material-ui/core';
import Badge from '@material-ui/core/Badge';
import { makeStyles } from '@material-ui/core/styles';
import './App.css';

const useStyles = makeStyles({
  appHeading: {
    textAlign: 'center',
    margin: 0,
    backgroundColor: '#BBBBF1',
    position: 'fixed',
    width: '100%',
    paddingTop: '15px',
    paddingBottom: '4px',
    zIndex: '1000',
    opacity: '.95',
  },
  appHeadingBadge: {
    fontSize: '2.0em',
    fontFamily: 'Teko, sans-serif',
  },
  appHeadingText: {
    paddingRight: '8px'
  },
});

const App = () => {
  const apiEndpoint = "https://medalsapi.azurewebsites.net/api/country";
  const [ countries, setCountries ] = useState([]);
  const [ medals, setMedals ] = useState([]);
  const classes = useStyles();

  // this is the functional equivalent to componentDidMount
  useEffect(() => {
    // initial data loaded here
    let fetchedMedals = [
      { id: 1, name: 'gold', RGBColor: '#ffc107' },
      { id: 2, name: 'silver', RGBColor: '#c4cacc' },
      { id: 3, name: 'bronze', RGBColor: '#cd7f32' },
    ]
    setMedals(fetchedMedals);
    async function fetchCountries() {
      const { data : fetchedCountries } = await axios.get(apiEndpoint);
      let newCountries = [];
      fetchedCountries.forEach(country => {
        let newCountry = {
          id: country.id,
          name: country.name,
        };
        fetchedMedals.forEach(medal => {
          const count = country[medal.name];
          newCountry[medal.name] = { current: count, saved: count };
        });
        newCountries.push(newCountry);
      });
      setCountries(newCountries);
    }
    fetchCountries();
  }, []);


  const handleAdd = async (name) => {
    const { data: post } = await axios.post(apiEndpoint, { name: name });
    let newCountry = { 
      id: post.id, 
      name: post.name,
    };
    medals.forEach(medal => {
      const count = post[medal.name];
      newCountry[medal.name] = { current: count, saved: count };
    });
    setCountries(countries.concat(newCountry));
  }
  const handleDelete = async (countryId) => {
    const originalCountries = countries;
    setCountries(countries.filter(c => c.id !== countryId));
    try {
      await axios.delete(`${apiEndpoint}/${countryId}`);
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        // country already deleted
        console.log("The record does not exist - it may have already been deleted");
      } else { 
        alert('An error occurred while deleting');
        setCountries(originalCountries);
      }
    }
  }
  const handleUpdate = async (countryId) => {
    const originalCountries = countries;

    const idx = countries.findIndex(c => c.id === countryId);
    const mutableCountries = [ ...countries ];
    const country = mutableCountries[idx];
    let jsonPatch = [];
    medals.forEach(medal => {
      if (country[medal.name].current !== country[medal.name].saved) {
        jsonPatch.push({ op: "replace", path: medal.name, value: country[medal.name].current });
        country[medal.name].saved = country[medal.name].current;
      }
    });
    console.log(`json patch for id: ${countryId}: ${JSON.stringify(jsonPatch)}`);
    // update state
    setCountries(mutableCountries);

    try {
      await axios.patch(`${apiEndpoint}/${countryId}`, jsonPatch);
    } catch (ex) {
      if (ex.response && ex.response.status === 404) {
        // country already deleted
        console.log("The record does not exist - it may have already been deleted");
      } else { 
        alert('An error occurred while updating');
        setCountries(originalCountries);
      }
    }
  }
  const handleReset = (countryId) => {
    const idx = countries.findIndex(c => c.id === countryId);
    const mutableCountries = [ ...countries ];
    const country = mutableCountries[idx];
    medals.forEach(medal => {
      country[medal.name].current = country[medal.name].saved;
    });
    setCountries(mutableCountries);
  }
  const handleIncrement = (countryId, medalName) => {
    const idx = countries.findIndex(c => c.id === countryId);
    const mutableCountries = [...countries ];
    mutableCountries[idx][medalName].current += 1;
    setCountries(mutableCountries);
  }
  const handleDecrement = (countryId, medalName) => {
    const idx = countries.findIndex(c => c.id === countryId);
    const mutableCountries = [...countries ];
    mutableCountries[idx][medalName].current -= 1;
    setCountries(mutableCountries);
  }
  const getAllMedalsTotal = () => {
    let sum = 0;
    medals.forEach(medal => { sum += countries.reduce((a, b) => a + b[medal.name].current, 0); });
    return sum;
  }
  return (
    <React.Fragment>
      <div className={classes.appHeading}>
        <Badge 
          className={classes.appHeadingBadge}
          badgeContent={ getAllMedalsTotal() } 
          color='primary'>
          <div className={classes.appHeadingText}>Olympic Medals</div>
        </Badge>
      </div>
      <Container fixed={true} style={{paddingTop: '70px', paddingBottom: '20px'}}>
        <NewCountry onAdd={ handleAdd } />
        <Grid spacing={2} justifyContent='center' container>
          { countries.map(country => 
            <Grid item key={ country.id }>
              <Country 
                country={ country } 
                medals={ medals }
                onDelete={ handleDelete }
                onUpdate={ handleUpdate }
                onReset={ handleReset }
                onIncrement={ handleIncrement } 
                onDecrement={ handleDecrement } />
            </Grid>
          )}
        </Grid>
      </Container>
    </React.Fragment>
  );
}
 
export default App;
